<template>
    <div class="table-container">
        <table>
            <thead>
                <tr>
                    <th>Mã nhân viên</th>
                    <th>Họ và tên</th>
                    <th>Giới tính</th>
                    <th>Ngày sinh</th>
                    <th>Điện thoại</th>
                    <th>Email</th>
                    <th>Chức vụ</th>
                    <th>Phòng ban</th>
                    <th>Mức lương cơ bản</th>
                    <th>Tình trạng công việc</th>
                </tr>
            </thead>
            <tbody v-if="searchResult.length">
                <tr v-for="employee in searchResult" :key="employee.id" @click="$emit('click-row', $event.target.parentElement)">
                    <td>{{employee.id}}</td>
                    <td>{{employee.hoVaTen}}</td>
                    <td>{{employee.gioiTinh}}</td>
                    <td>{{employee.ngaySinh}}</td>
                    <td>{{employee.dienThoai}}</td>
                    <td>{{employee.email}}</td>
                    <td>{{employee.chucVu}}</td>
                    <td>{{employee.phongBan}}</td>
                    <td class="salary-col">{{employee.luong}}</td>
                    <td>{{employee.tinhTrang}}</td>
                </tr>
            </tbody>
            <div v-else>Danh sách rỗng</div>
        </table>
    </div>
</template>

<script>
export default {
    props: {
        searchString: String,
        employeeProps: Object
    },
    data() {
        return {
        }
    },
    computed: {
        searchResult() {
            let temp = []
            let pattern = new RegExp(`${this.searchString}`, 'i')
            this.employeeProps.forEach(element => {
                if (pattern.test(element.maNhanVien) || 
                    pattern.test(element.hoVaTen) || 
                    pattern.test(element.dienThoai)) {
                        temp.push(element)
                    }
            });
            return temp
        }
    },
    methods: {
    },
}
</script>
<style scoped>
    .table-container {
    height: calc(100vh - 206px);
    overflow-y: scroll;
    /* overflow-x: scroll; */
    margin: 0 16px;
    box-sizing: border-box;
    }
    table {
        width: 100%;
        height: fit-content;
        display: table;
        overflow-x: auto;
        white-space: nowrap;
        font-size: 13px;
    }
    thead th { 
        position: sticky; 
        top: 0; 
        background-color: #fff;
    }
    thead, tbody, tr {
        width: 100%;
    }
    tr {
        height: 40px;
    }
    table td, table th {
        border-bottom: 1px solid #ddd;
        padding: 8px;
        text-align: left;
    }
    .salary-col {
        text-align: right;
    }
    tbody tr:nth-child(odd){
        background-color: #e5e5e5;

        
    }

    /* width */
    /* ::-webkit-scrollbar- */
    ::-webkit-scrollbar {
        width: 8px;
        border: solid;
    }

    /* Track */
    ::-webkit-scrollbar-track {
        background: #f1f1f1;
    }

    /* Handle */
    ::-webkit-scrollbar-thumb {
        background: #888;
        border-radius: 4px;

    }

    /* Handle on hover */
    ::-webkit-scrollbar-thumb:hover {
        background: #555;
    }
</style>