<template>
    <form :action="postedForm ? 'https://localhost:7198/api/Employee' : ('https://localhost:7198/api/Employee/' + formFill.id)"
        method="POST"
        class="form-main"
        enctype="application/json"
    >
        <div class="avatar-user">
            <img src="/assets/img/default-avatar.jpg" alt="">
            <p>(Vui lòng chọn ảnh có định dạng .jpg, .jpeg, .png, .gif.)</p>
        </div>
        <div class="form">
            <div class="overall-info">
                <FormTitle title='A. THÔNG TIN CHUNG'/>
                <div class="form-input-grid">
                    <InputLbl 
                        type="text"
                        label="Mã nhân viên"
                        :required=true
                        :value="maNhanVien"
                        :name="'id'"
                        :disabled="!postedForm"
                    />
                    <InputLbl 
                        type="text"
                        label="Họ và tên"
                        :required=true
                        :value="hoVaTen"
                        :name="'hoVaTen'"
                    />
                    <InputLbl 
                        type="date"
                        label="Ngày sinh"
                        :required=false
                        :value="ngaySinh"
                        :name="'ngaySinh'"
                    />
                    <InputLbl 
                        type="text"
                        label="Giới tính"
                        :required=true
                        :value="gioiTinh"
                        :name="'gioiTinh'"
                    />
                    <InputLbl 
                        type="text"
                        label="Số CMTND/ Căn cước"
                        :required=true
                        :name="'cmtnd'"
                        :value="cmtnd"
                    />
                    <InputLbl 
                        type="date"
                        label="Ngày cấp"
                        :required=false
                        :name="'dateCmtnd'"
                    />
                    <InputLbl 
                        type="text"
                        label="Nơi cấp"
                        :required=false
                        :name="'addressCmtnd'"
                    />
                    <InputLbl 
                        style="grid-column: 1 / 2"
                        type="email"
                        label="Email"
                        :required=true
                        :value="email"
                        :name="'email'"
                    />
                    <InputLbl 
                        type="text"
                        label="Số điện thoại"
                        :required=true
                        :value="dienThoai"
                        :name="'dienThoai'"
                    />
                </div>
            </div>
            <div class="work-info">
                <FormTitle title='B. THÔNG TIN CÔNG VIỆC'/>
                <div class="form-input-grid">
                    <InputLbl 
                        type="text"
                        label="Vị trí"
                        :required=false
                        :value="chucVu"
                        :name="'chucVu'"
                    />
                    <InputLbl 
                        type="text"
                        label="Phòng ban"
                        :required=false
                        :value="phongBan"
                        :name="'phongBan'"
                    />
                    <InputLbl 
                        type="text"
                        label="Mã số thuế cá nhân"
                        :required=false
                        :name="'tax'"
                    />
                    <InputLbl 
                        type="text"
                        label="Mức lương cơ bản"
                        :required=false
                        :value="luong"
                        :name="'luong'"
                    />
                    <InputLbl 
                        type="date"
                        label="Ngày gia nhập công ty"
                        :required=false
                        :name="'date_join'"
                    />
                    <InputLbl 
                        type="text"
                        label="Tình trạng công việc"
                        :required=false
                        :value="tinhTrang"
                        :name="'tinhTrang'"
                    />
                </div>
            </div>
        </div>

        <FormFooter/>
    </form>
</template>

<script>
import FormTitle from './FormTitle.vue'
import InputLbl from '../InputLbl.vue'
import FormFooter from './FormFooter.vue'
export default {
    name: 'FormMain',
    components: {
        FormTitle, FormFooter, InputLbl
    },
    props: {
        formFill: Object,
        postedForm: Boolean
    },
    computed: {
        maNhanVien() {
            if (this.formFill !== {})
                // {return this.formFill.childNodes[0].textContent}
                {return this.formFill.id}
            else return ''
        },
        hoVaTen() {
            if (this.formFill)
                // return this.formFill.childNodes[1].textContent
                return this.formFill.hoVaTen
            else return ''
        },
        gioiTinh() {
            if (this.formFill)
                // return this.formFill.childNodes[2].textContent
                return this.formFill.gioiTinh
            else return ''
        },
        ngaySinh() {
            if (this.formFill)
                // return this.formFill.childNodes[3].textContent
                return this.formFill.ngaySinh
            else return ''
        },
        dienThoai() {
            if (this.formFill)
                // return this.formFill.childNodes[4].textContent
                return this.formFill.dienThoai
            else return ''
        },
        email() {
            if (this.formFill)
                // return this.formFill.childNodes[5].textContent
                return this.formFill.email
            else return ''
        },
        chucVu() {
            if (this.formFill)
                // return this.formFill.childNodes[6].textContent
                return this.formFill.chucVu
            else return ''
        },
        phongBan() {
            if (this.formFill)
                // return this.formFill.childNodes[7].textContent
                return this.formFill.phongBan
            else return ''
        },
        luong() {
            if (this.formFill)
                // return this.formFill.childNodes[8].textContent
                return this.formFill.luong
            else return ''
        },
        tinhTrang() {
            if (this.formFill)
                // return this.formFill.childNodes[9].textContent
                return this.formFill.tinhTrang
            else return ''
        },
        cmtnd() {
            if (this.formFill)
                // return this.formFill.childNodes[9].textContent
                return this.formFill.cmtnd
            else return ''
        },
    }
}
</script>

<style scoped>
.form-main {
    display: flex;
    position: relative;
    width: 100%;
    padding: 0 16px 52px 16px;
}
.avatar-user {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 160px;
    margin-right: 12px;
}
.avatar-user > img {
    width: 160px;
    height: 160px;
    border-radius: 50%;
    border: 1px solid #bbb;
}
.avatar-user > p {
    text-align: center;
    font-size: 13px;
    font-weight: lighter;
}
.form {
    width: 100%;
}
.form > div {
    margin-bottom: 24px;
}
.form-input-grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 8px;
    row-gap: 12px;
    width: 100%;
}
</style>