<template>
    <input class="input-icon" :style="styleBackground" type="text" :placeholder="placeholder">    
</template>

<script>
export default {
    name: 'InputIcon',
    props: {
        icon: {
            type: String,
            default: ''
        },
        placeholder: String
    },
    data() {
        return {
            styleBackground: {
                background: 'url(' + this.iconUrl(this.icon) + ') no-repeat left 16px',
                'background-position': '16px'
            }
        }
    },
    methods: {
        iconUrl(icon) {
            switch (icon) {
                case 'search': {
                    return '/assets/icon/search.png'
                }
                case 'add': {
                    return '/assets/icon/add.png'
                }
                case 'option': {
                    return '../assets/icon/option.png'
                }
                case 'refresh': {
                    return '../assets/icon/refresh.png'
                }
                default: {
                    return ''
                }
            }
        }
    },
    
}
</script>

<style scoped>
    .input-icon {
        display: flex;
        flex-direction: row;
        align-items: center;
        padding-left: 40px;
        width: fit-content;
    }

    .input-icon:focus {
        outline: none;
        border: #019160 solid 1px;
    }
    /* .input-icon-search {
        margin-right: 16px;
        width: 300px;
        background: url('../../resource/icon/search.png') no-repeat left 16px;
        background-position: 16px;
    } */

    .input-icon:focus::placeholder {
        visibility: hidden;
    }

    ::placeholder {
        color: #bbb;
    }
    .input-icon::placeholder {
        font-size: 12px;
    }
   
</style>