<template>
  <MenuLeft/>
  <MainScreen @show-form="showForm" @click-row="clickRow($event)"/>
  <FormBackground 
    v-if="!closedForm" 
    @close-form="closeForm"
    :formFill="formFill"
    :postedForm="postedForm"
    @click="clickBack"
  />
</template>

<script>
import MenuLeft from './components/MenuLeft.vue'
import MainScreen from './components/main-screen/MainScreen.vue'
import FormBackground from './components/form-info/FormBackground.vue'
export default {
  name: 'App',
  components: {
    MenuLeft, MainScreen, FormBackground
  },
  data() {
    return {
      closedForm: true,
      postedForm: false,
      formFill: {}
    }
  },
  methods: {
    closeForm() {
      this.closedForm = true
    },
    showForm() {
      this.closedForm = false
      this.postedForm = true
      this.formFill = {}
    },
    async clickRow(e) {
      let response = await fetch(`https://localhost:7198/api/Employee/${e.childNodes[0].textContent}`)
      let data = await response.json()
      this.closedForm = false
      this.postedForm = false
      this.formFill = data
    },
    clickBack(e) {
      if(e.target.classList.contains('form-background')) {
        this.closedForm = true
      }
    }
  },
  mounted() {
    this.formFill = {}
  }
}
</script>

