<template>
    <button type="submit" v-if="type == 'submit'" class="btn-icon" :style="{'background-image': 'url('+ imgSrc +')'}">{{ text }}</button>
    <div v-else class="btn-icon" :style="{'background-image': 'url('+ imgSrc +')'}">{{ text }}</div>
</template>

<script>
export default {
    name: 'ButtonIcon',
    props: {
        text: String,
        imgSrc: String,
        type: String
    }
}
</script>

<style scoped>
    .btn-icon {
    border: none;
    border-radius: 4px;
    padding: calc((40px - 1em)/2) 16px calc((40px - 1em)/2) 40px;
    font-size: 13px;
    cursor: pointer;
    color: #fff;
    height: 40px;
    text-align: center;
    background-color: #019160;
    background-size: 16px 16px;
    background-repeat: no-repeat;
    background-position: left;
    background-position: 16px;
    }

    .btn-icon:hover {
        background-color: #2fbe8e;
    }
    .btn-icon:active {
        background-color: #01b075;
    }
</style>